import LogicElementUtil from "../../contents/create/factory/util/logicElementUtil";
import LogicUtil from "../../contents/create/factory/util/logicUtil";
import StoryUtil from "../../contents/create/launcher/util/storyUtil";
import BuildUtil from "../../contents/create/preview/buildUtil";
import PreviewLauncherFrame from "../../contents/create/preview/previewLauncherFrame";
import CanvasUtil from "../../utils/canvasUtil";
import { Store } from "../store/store";

export class Dispatcher {

    private store: Store;
    private setStore: (store: Store) => void;

    constructor(store: Store, setStore: (store: Store) => void) {
        this.store = store;
        this.setStore = setStore;
    }

    public updateStore = () => {
        this.setStore({ ...this.store });
    }

    public system = {
        // setUserEnv: (userEnv: UserEnv) => {
        // },
    }
    public story = {

        /**
         * プレビューオールダイアログを表示する
         */
        openPreviewAllDialog: () => {
            const storyData = this.store.storyData as StoryUtil.StoryData;

            const fuseList = storyData.fuseList;
            const limitFrame = storyData.limitFrame;
            const limitParticle = storyData.limitParticle;
            const minFrame = fuseList[0].frame;
            const logicData = this.store.libraryData as LogicUtil.WrapElement;
            const sceens: string[] = [];
            let useFrame: number = 0;
            BuildUtil.run({
                limitFrame,
                limitParticle,
                guaranteeFrame: minFrame,
                doIterate: (frame, owner) => {

                    fuseList.some(def => {
                        if (def.frame === frame) {
                            LogicElementUtil.doParticleJob(logicData, def, owner);
                        } else if (def.frame > frame) return 1;
                    });

                    sceens.push(CanvasUtil.createURL(550, 550, (ctx) => {
                        // パーティクルの描画
                        owner.forEach(par => {
                            par.draw(ctx);
                        });
                        ctx.save();
                    }));
                },
                doEnd: (frame) => {
                    useFrame = frame;
                }
            });
            this.store.system.dialog = (
                <PreviewLauncherFrame.Component
                    sceens={sceens}
                    frameCnt={useFrame}
                />
            );
            this.updateStore();
        },

        /**
         * プレビューソロダイアログを表示する
         */
        openPreviewSoloDialog: (focus: number) => {
            const storyData = this.store.storyData as StoryUtil.StoryData;

            const fuseList = storyData.fuseList;
            const limitFrame = storyData.limitFrame;
            const limitParticle = storyData.limitParticle;
            const logicData = this.store.libraryData as LogicUtil.WrapElement;
            const sceens: string[] = [];
            let useFrame: number = 0;
            BuildUtil.run({
                limitFrame,
                limitParticle,
                doIterate: (frame, owner) => {

                    const def = fuseList[focus];
                    if (frame === 0) {
                        LogicElementUtil.doParticleJob(logicData, def, owner);
                    }

                    sceens.push(CanvasUtil.createURL(550, 550, (ctx) => {
                        // パーティクルの描画
                        owner.forEach(par => {
                            par.draw(ctx);
                        });
                        ctx.save();
                    }));
                },
                doEnd: (frame) => {
                    useFrame = frame;
                }
            });
            this.store.system.dialog = (
                <PreviewLauncherFrame.Component
                    sceens={sceens}
                    frameCnt={useFrame}
                />
            );
            this.updateStore();
        },


        updateCache: (fuse: StoryUtil.Fuse, cache: StoryUtil.Cache) => {
            const logicData = this.store.libraryData as LogicUtil.WrapElement;

            const storyData = this.store.storyData as StoryUtil.StoryData;
            const fuseList = storyData.fuseList;
            const limitFrame = storyData.limitFrame;
            const limitParticle = storyData.limitParticle;
            
            cache.thumbList = [];
            cache.useFrame = 0;
            const seedProps = LogicElementUtil.getSeedProperties(logicData, fuse.seedId);
            BuildUtil.run({
                limitFrame,
                limitParticle,
                doIterate: (frame, owner) => {
                    if (frame === 0) {
                        LogicElementUtil.doParticleJob(logicData, fuse, owner);
                    }

                    const thumb = seedProps.thumbnailList.find(thumb => thumb.frame === frame);
                    if (thumb != undefined) {
                        const url = CanvasUtil.createURL(550, 550, (ctx) => {
                            // パーティクルの描画
                            owner.forEach(par => {
                                par.draw(ctx);
                            });
                            ctx.save();
                        });
                        cache.thumbList.push({ name: thumb.name, url });
                    }
                },
                doEnd: (frame) => {
                    cache.useFrame = frame;
                }
            });
        }
    }
}