import StoryUtil from "../../contents/create/launcher/util/storyUtil";
import StoreCloud from "./storeCloud";
import StoreSystem from "./storeSystem";

/**
 * システムで使用するステート群
 */
export type Store = {
    system: StoreSystem.Param;
    env: StoreSystem.Env;
    libraryData: LogicUtil.WrapElement | null;
    storyData: StoryUtil.StoryData | null;
    fuseCacheList: StoryUtil.Cache[];
    cloud: StoreCloud.Props | null;
    fileHandle: FileSystemFileHandle | null;
}


export const ProjectTypes = ['library', 'story'] as const;
export type ProjectType = typeof ProjectTypes[number];

export const initialStore: Store = {
    system: {
        isSplash: true,
        user: null,
        dialog: null,
        canDialogClose: false,
        isSPMode: false,
        logoutActions: []
    },
    env: {
        isEditLibrary: true,
        isUseLocalFile: true,
        screenRate: 1,
        scrollLock: false,
    },
    libraryData: null,
    storyData: null,
    fuseCacheList: [],
    cloud: null,
    fileHandle: null,
}