import { useContext, useMemo, useState, useEffect } from "react";
import FormUtil from "../../../utils/form/formUtiil";
import FuseList from "./fuseList";
import { GlobalContext } from "../../../contents/entry/systemEntry";
import StoryUtil from "../../../contents/create/launcher/util/storyUtil";
import FuseMainteDialog from "./fuseMainteDialog";
import { FuseBean } from "../../../contents/create/launcher/storyTab";
import LogicUtil from "../../../contents/create/factory/util/logicUtil";
import LogicElementUtil from "../../../contents/create/factory/util/logicElementUtil";
import BuildUtil from "../../../contents/create/preview/buildUtil";
import { AiOutlineAppstoreAdd } from "react-icons/ai";
import { FaSortAmountDownAlt } from "react-icons/fa";
import { VscPreview } from "react-icons/vsc";
import { RiDeleteBinLine } from "react-icons/ri";
import { IoSave } from "react-icons/io5";


import styled, { css } from "styled-components";
import ValidateUtil from "../../../utils/form/validateUtil";
import FrameLine, { FRAME_LINE_WIDTH } from "./frameLine";
import assert from "assert";

const SPEditStoryFrame = () => {
    const { store, setStore, dispatcher } = useContext(GlobalContext);

    const [focus, setFocus] = useState<number>(-1);
    const [isPreview, setPreview] = useState<boolean>(false);
    // const [seedId, setSeedId] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [presetIndex, setPresetIndex] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [interval, setInterval] = useState<FormUtil.CheckableValue>({ value: '30', errors: [] });

    const storyData = store.storyData as StoryUtil.StoryData;

    const fuseList = storyData.fuseList;
    const limitFrame = storyData.limitFrame;
    const limitParticle = storyData.limitParticle;

    const [seedList, presetList]: [FuseBean[], StoryUtil.PresetBean[]] = useMemo(() => {
        // if (store.libraryData == null) throw assert('store.libraryDataがnullです。');
        assert(store.libraryData != null);
        const root = store.libraryData.data as LogicUtil.NodeRoot;
        const seedWrapList = (root.seedMng.data as LogicUtil.NodeSeedManager).defs;

        const seedList: FuseBean[] = [];
        const presetList: StoryUtil.PresetBean[] = [];
        seedWrapList.forEach(wrap => {
            const seed = wrap.data as LogicUtil.NodeSeedItem;

            // 引数リストを取得
            const argMng = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng', 'seedarg-mng').data as LogicUtil.NodeSeedArgManager;
            const args = argMng.args.map(wrap => wrap.data as LogicUtil.NodeSeedArgItem);

            // フォームグループリストを取得
            const groupMng = LogicElementUtil.getInnerWrap(wrap, 'interface-mng', 'formgrp-mng');
            let formgrps: LogicUtil.NodeFromGrpItem[] = [];
            if (groupMng != null) {
                formgrps = (groupMng.data as LogicUtil.NodeFromgrpManager).defines.map(wrap => wrap.data as LogicUtil.NodeFromGrpItem);
            }

            // プリセットリストを取得
            const sampleMng = LogicElementUtil.getInnerWrap(wrap, 'sample-mng')?.data as LogicUtil.NodeSampleManager;
            const presets = sampleMng.samples.map(wrap => wrap.data as LogicUtil.NodeSampleItem);

            seedList.push({
                name: seed.name,
                disp: seed.disp,
                args, formgrps, presets
            });

            presets.forEach(preset => {
                const dispName = (seed.disp ?? seed.name) + ': ' + preset.name;
                presetList.push({
                    seedId: seed.name,
                    dispName,
                    values: preset.values
                });
            });
        });
        return [seedList, presetList];
    }, [store.libraryData]);

    useEffect(() => {
        const isLoad = fuseList.length > 0 && store.fuseCacheList.length === 0;
        if (!isLoad) return;
        fuseList.forEach(fuse => {
            const cache = { seq: fuse.seq, thumbList: [], useFrame: 0 };
            store.fuseCacheList.push(cache);
            dispatcher.story.updateCache(fuse, cache);
        });
        storyData.fuseList = fuseList.slice();
        dispatcher.updateStore();
    }, [fuseList, store.fuseCacheList]);

    const getNewFuseProps = (): {
        newSeq: number;
        newFrame: number;
    } => {
        const newSeq = fuseList
            .map(fuse => fuse.seq)
            .reduce((prev, cur) => prev < cur ? cur : prev, -1)
            + 1;
        const newFrame = fuseList.length === 0 ? 0 : fuseList[fuseList.length - 1].frame + Number(interval.value);
        return { newSeq, newFrame };
    }

    return (<>
        <FormUtil.ButtonRecord
            align="left"
            isIconButton
            buttons={[
                {
                    label: 'Preview',
                    width: 140,
                    icon: <VscPreview />,
                    isEnable: fuseList.length > 0,
                    callback: () => {
                        fuseList.sort((a, b) => {
                            return a.frame - b.frame;
                        });
                        storyData.fuseList = fuseList.slice();
                        // dispatcher.updateStore();
                        setPreview(true);
                    }
                },
                {
                    label: 'Save',
                    width: 140,
                    icon: <IoSave />,
                    callback: () => {
                        alert('仕様検討中');
                    }
                }
            ]}
        />
        <FormUtil.BorderPanel
            title="fuse"
            height={660}
            backgroundColor="#c9d4dd"
            innerJsx={<>
                <_SeedDiv>
                    <FormUtil.ButtonItem
                        button={
                            {
                                label: 'Add',
                                width: 100,
                                icon: <AiOutlineAppstoreAdd />,
                                callback: () => {
                                    if (presetIndex.value === '') {

                                        store.system.dialog = <FuseMainteDialog
                                            focus={-1}
                                            seedList={seedList}
                                            cancelAction={() => {
                                            }}
                                            modifyAction={(frame: number, seedId: string, values: number[]) => {
                                            }}
                                        />;
                                        setStore({ ...store });
                                    } else {
                                        const preset = presetList[Number(presetIndex.value)];
                                        console.log(preset);
                                        const seedId = preset.seedId;
                                        const seed = seedList.find(seed => seed.name === seedId) as FuseBean;
                                        const newFuseProps = getNewFuseProps();
                                        const fuse: StoryUtil.Fuse = {
                                            seq: newFuseProps.newSeq,
                                            frame: newFuseProps.newFrame,
                                            seedId: '',
                                            values: []
                                        };
                                        const cache = { seq: fuse.seq, thumbList: [], useFrame: 0 };

                                        fuseList.push(fuse);
                                        store.fuseCacheList.push(cache);

                                        fuse.seedId = seedId;
                                        fuse.dispName = seed.disp;
                                        fuse.values = preset.values.slice();
                                        dispatcher.story.updateCache(fuse, cache);
                                        (store.storyData as StoryUtil.StoryData).fuseList = fuseList.slice();
                                        store.system.dialog = null;
                                        dispatcher.updateStore();
                                    }

                                }
                            }
                        }
                    />
                    <_FormDiv>
                        <FormUtil.FormRecord
                            titleLabel="Preset"
                            jsx={
                                <FormUtil.Combobox
                                    checkable={presetIndex}
                                    setCheckable={setPresetIndex}
                                    list={presetList.map((preset, i) => {
                                        return { value: i.toString(), labelText: preset.dispName }
                                    })}
                                    headBlank
                                    width={340}
                                // validates={
                                //     [
                                //         {
                                //             checker: (value) => ValidateUtil.checkRequired(value),
                                //             errorType: "required"
                                //         }
                                //     ]
                                // }
                                />
                            }
                        />
                        <FormUtil.FormRecord
                            titleLabel="Interval"
                            jsx={
                                <FormUtil.TextField
                                    width={100}
                                    isNumber
                                    checkable={interval}
                                    setCheckable={setInterval}
                                    validates={[
                                        {
                                            checker: (value) => ValidateUtil.checkRequired(value),
                                            errorType: "required"
                                        },
                                        {
                                            checker: (value) => ValidateUtil.checkNumberRange(value, 0, storyData.limitFrame),
                                            errorType: "value"
                                        }
                                    ]}
                                />
                            }
                        />
                    </_FormDiv>
                </_SeedDiv>
                <_MainDiv>
                    <FrameLine
                        focus={focus}
                        setFocus={setFocus}
                        seedList={seedList}
                    />
                    <_FuseListFrame>
                        <FuseList
                            focus={focus}
                            setFocus={setFocus}
                            seedList={seedList}
                        />
                    </_FuseListFrame>
                </_MainDiv>
            </>}
        />
        {!isPreview ? <></> : <BuildUtil.BuildFrame closeAction={() => {
            setPreview(false);
        }} />}
    </>);
}

export default SPEditStoryFrame;


const _SeedDiv = styled.div`
  display: inline-block;
  position: relative;
  width: 100%;
  height: 84px;
  /* background-color: #be97ca; */
  vertical-align: top;
`;
const _FormDiv = styled.div`
  display: inline-block;
  position: relative;
  width: 492px;
  vertical-align: top;
  /* background-color: #09fc3e; */
  margin: 0 0 0 4px;
`;

const _MainDiv = styled.div`
  display: inline-block;
  position: relative;
  width: 100%;
  height: calc(100% - 84px);
  /* background-color: #c2ca97; */
`;

const _FuseListFrame = styled.div`
  display: inline-block;
  position: relative;
  width: calc(100% - ${FRAME_LINE_WIDTH}px);
  height: 100%;
  /* background-color: #f80707; */
  vertical-align: top;
`;