import styled from "styled-components";
import Styles from "../../../../base/design/styles";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import DrawUtil from "../../../graphic/drawUtil";
import LogicUtil from "./logicUtil";
import DrawableVectorChooser from "./drawableVectorChooser";

namespace LogicFormUtil {

    export const buildPreviewForms = (
        isReadOnly: boolean,
        argumentList: LogicUtil.NodeSeedArgItem[],
        formgrpList: LogicUtil.NodeFromGrpItem[],
        formList: FormUtil.CheckableValue[],
        setFormList: ((formList: FormUtil.CheckableValue[]) => void)
    ): JSX.Element[] => {
        /** 入力フォームのJSX */
        const formJsxList: JSX.Element[] = [];
        const usedGrps: string[] = [];
        formList.forEach((form, i) => {
            const argument = argumentList[i];
            const formgrp = formgrpList.find(grp => grp.args.includes(argument.name));

            // フォームグループが設定されている場合、独自の入力UIを表示する
            if (formgrp != undefined) {
                // 設定済みであれば追加しない
                if (usedGrps.includes(formgrp.name)) return 1;
                // 利用済みリストに追加
                usedGrps.push(formgrp.name);
                const targetForms: number[] = [];
                argumentList.forEach((arg, j) => {
                    if (formgrp.args.includes(arg.name)) {
                        targetForms.push(j);
                    }
                });
                switch (formgrp.method) {
                    case 'color': {
                        const colorCode = DrawUtil.convertColorCodeFromRgba({
                            r: Number(formList[targetForms[0]].value),
                            g: Number(formList[targetForms[1]].value),
                            b: Number(formList[targetForms[2]].value),
                            a: 255
                        }).substring(0, 7);
                        // console.log(colorCode);
                        formJsxList.push(<div key={i}>
                            <FormUtil.Record
                                jsx={
                                    <_Message>{formgrp.name}: <_Type>color </_Type><_Range>({0} ～ {255} respectively)</_Range></_Message>
                                }
                            />
                            <FormUtil.Record
                                jsx={<>
                                    {targetForms.map((formIndex, j) => {
                                        return (
                                            <FormUtil.TextField
                                                key={formIndex}
                                                width={72}
                                                marginLeft={j === 0 ? 5 : 4}
                                                isNumber
                                                isReadOnly={isReadOnly}
                                                checkable={formList[formIndex]}
                                                setCheckable={() => {
                                                    setFormList(formList.slice());
                                                }}
                                                validates={[
                                                    {
                                                        checker: (value) => ValidateUtil.checkRequired(value),
                                                        errorType: "required"
                                                    },
                                                    {
                                                        checker: (value) => ValidateUtil.checkNumberRange(value, argument.min as number, argument.max as number),
                                                        errorType: 'value'
                                                    }
                                                ]}
                                            />
                                        );
                                    })}
                                    <_Color type="color" value={colorCode}
                                        disabled={isReadOnly}
                                        onChange={(value) => {
                                            const rgba = DrawUtil.convertRgbaFromColorCode(value.target.value + 'ff');
                                            if (rgba != null) {
                                                formList[targetForms[0]].value = rgba.r.toString();
                                                formList[targetForms[1]].value = rgba.g.toString();
                                                formList[targetForms[2]].value = rgba.b.toString();
                                            }
                                            setFormList(formList.slice());
                                        }}
                                    />
                                </>}
                            />
                        </div>);
                    } break;
                    case 'draw': {
                        const xValue = formList[targetForms[0]].value;
                        const yValue = formList[targetForms[1]].value;
                        const xList = (xValue === '' ? [] : xValue.split(',').map(v => Number(v)));
                        const yList = (xValue === '' ? [] : yValue.split(',').map(v => Number(v)));
                        formJsxList.push(<div key={i}>
                            <FormUtil.Record
                                jsx={
                                    <_Message>{formgrp.name}: <_Type>point </_Type><_Range>(～ {255} drawing)</_Range></_Message>
                                }
                            />
                            <DrawableVectorChooser.ComponentNormal
                                addPoint={(x, y) => {
                                    xList.push(x);
                                    yList.push(y);
                                    formList[targetForms[0]].value = xList.join(',');
                                    formList[targetForms[1]].value = yList.join(',');
                                    setFormList(formList.slice());
                                }}
                                clear={() => {
                                    formList[targetForms[0]].value = '';
                                    formList[targetForms[1]].value = '';
                                    setFormList(formList.slice());
                                }}
                                xList={xList}
                                yList={yList}
                            />
                        </div>);
                    } break;
                    case 'draw-colorable': {
                        const xValue = formList[targetForms[0]].value;
                        const yValue = formList[targetForms[1]].value;
                        const rValue = formList[targetForms[2]].value;
                        const gValue = formList[targetForms[3]].value;
                        const bValue = formList[targetForms[4]].value;
                        const xList = (xValue === '' ? [] : xValue.split(',').map(v => Number(v)));
                        const yList = (xValue === '' ? [] : yValue.split(',').map(v => Number(v)));
                        const rList = (rValue === '' ? [] : rValue.split(',').map(v => Number(v)));
                        const gList = (gValue === '' ? [] : gValue.split(',').map(v => Number(v)));
                        const bList = (bValue === '' ? [] : bValue.split(',').map(v => Number(v)));
                        formJsxList.push(<div key={i}>
                            <FormUtil.Record
                                jsx={
                                    <_Message>{formgrp.name}: <_Type>point </_Type><_Range>(～ {255} drawing)</_Range></_Message>
                                }
                            />
                            <DrawableVectorChooser.ComponentColorable
                                addPoint={(x, y, r, g, b) => {
                                    xList.push(x);
                                    yList.push(y);
                                    rList.push(r);
                                    gList.push(g);
                                    bList.push(b);
                                    formList[targetForms[0]].value = xList.join(',');
                                    formList[targetForms[1]].value = yList.join(',');
                                    formList[targetForms[2]].value = rList.join(',');
                                    formList[targetForms[3]].value = gList.join(',');
                                    formList[targetForms[4]].value = bList.join(',');
                                    setFormList(formList.slice());
                                }}
                                clear={() => {
                                    formList[targetForms[0]].value = '';
                                    formList[targetForms[1]].value = '';
                                    formList[targetForms[2]].value = '';
                                    formList[targetForms[3]].value = '';
                                    formList[targetForms[4]].value = '';
                                    setFormList(formList.slice());
                                }}
                                xList={xList}
                                yList={yList}
                                rList={rList}
                                gList={gList}
                                bList={bList}
                            />
                        </div>);
                    } break;
                }
            } else {
                let labelName = argument.name;
                if (argument.disp != undefined) labelName = argument.disp;
                formJsxList.push(<div key={i}>
                    <FormUtil.Record
                        jsx={
                            <_Message>{labelName}: <_Type>number </_Type><_Range>({argument.min} ～ {argument.max})</_Range></_Message>
                        }
                    />
                    <FormUtil.Record
                        jsx={<>
                            <FormUtil.TextField
                                width={142}
                                marginLeft={6}
                                isNumber
                                isReadOnly={isReadOnly}
                                step={argument.step}
                                checkable={form}
                                setCheckable={() => {
                                    setFormList(formList.slice());
                                }}
                                validates={[
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    },
                                    {
                                        checker: (value) => ValidateUtil.checkNumberRange(value, argument.min as number, argument.max as number),
                                        errorType: 'value'
                                    }
                                ]}
                            />
                            <_Tsumami type="range" value={Number(form.value)} min={argument.min} max={argument.max} step={argument.step}
                                disabled={isReadOnly}
                                onChange={(value) => {
                                    formList[i] = { ...form, value: value.target.value };
                                    setFormList(formList.slice());
                                }}
                            />
                        </>}
                    />
                </div>);
            }
        });
        return formJsxList;
    }
}

export default LogicFormUtil;



const _Message = styled.div<{
}>`
    display: inline-block;
    height: calc(100% - 4px);
    margin-top: 2px;
    font-size: ${Styles.FONT_MIDIUM}px;
    line-height: 32px;
    /* font-weight: 600; */
    border-radius: 4px;
    padding: 0 5px;
    /* background-color: #d37878; */
    text-align: left;
    vertical-align: top;
    user-select: text;
    overflow: hidden;
    /* white-space: nowrap; */
`;

const _Type = styled.span`
    color: #c10000;
    font-weight: 600;
`;
const _Range = styled.span`
    color: #423e84fb;
`;

const _Tsumami = styled.input`
    display: inline-block;
    vertical-align: top;
    margin: 11px 0 0 6px;
    width: calc(100% - 160px);
`;

const _Color = styled.input`
    display: inline-block;
    vertical-align: top;
    margin: 3px 0 0 6px;
    width: 100px;
    height: 34px;
`;