import { useState, useMemo, useEffect } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import LogicElementUtil from "../../factory/util/logicElementUtil";
import LogicUtil from "../../factory/util/logicUtil";
import PreviewUtil from "../previewUtil";

const PreviewLogicInputForm = (props: {
    logicWrap: LogicUtil.WrapElement;
    setSendingValueMap: (sendingValueMap: (() => PreviewUtil.ValueMap[]) | null) => void;
    setSendingArrayMap: (sendingArrayMap: (() => PreviewUtil.ArrayMap[]) | null) => void;
}) => {

    const [formList, setFormList] = useState<FormUtil.CheckableValue[]>([]);

    const argumentList: LogicUtil.NodeArgumentItem[] = useMemo(() => {
        let list: LogicUtil.NodeArgumentItem[] = [];
        const argWrap = LogicElementUtil.getInnerWrap(props.logicWrap, 'argument-mng');
        if (argWrap != null) {
            list = (argWrap.data as LogicUtil.NodeArgumentManager).args.map(arg => {
                const argItem = arg.data as LogicUtil.NodeArgumentItem;
                return argItem;
            });
        }
        return list;
    }, []);

    useEffect(() => {
        setFormList(argumentList.map(arg => {
            return { value: '', errors: [] };
        }));
    }, []);

    useEffect(() => {
        const isFormInputOK = formList.find(form => form.errors.length > 0) == undefined;
        if (isFormInputOK) {
            props.setSendingValueMap(() => {
                return formList
                    .filter((form, i) => {
                        return argumentList[i].valueType === 'number';
                    })
                    .map((form, i) => {
                        const argument = argumentList[i];
                        return { name: argument.name, value: Number(form.value) } as PreviewUtil.ValueMap
                    });
            });
            props.setSendingArrayMap(() => {
                return formList
                    .filter((form, i) => {
                        return argumentList[i].valueType === 'array';
                    })
                    .map((form, i) => {
                        const argument = argumentList[i];
                        const array = form.value.split(',').map(v => Number(v));
                        return { name: argument.name, array } as PreviewUtil.ArrayMap
                    });
            });
        } else {
            props.setSendingValueMap(null);
            props.setSendingArrayMap(null);
        }
    }, [formList]);

    return <>{
        formList.map((form, i) => {
            const argument = argumentList[i];

            const validates: ValidateUtil.Validate[] = [];
            let isNumber = false;
            if (argument.valueType === 'number') {
                validates.push({
                    checker: (value) => ValidateUtil.checkRequired(value),
                    errorType: "required"
                });
                isNumber = true;
            } else {
                validates.push({
                    checker: (value) => ValidateUtil.checkArrayNumber(value),
                    errorType: "value"
                });
            }
            return (<FormUtil.FormRecord
                key={i}
                titleLabel={argument.name}
                labelWidth={250}
                jsx={
                    <FormUtil.TextField
                        width={312}
                        isNumber={isNumber}
                        checkable={form}
                        setCheckable={() => {
                            setFormList(formList.slice());
                        }}
                        validates={validates}
                    />
                }
            />);
        })
    }</>
}

export default PreviewLogicInputForm;